:root, [data-theme="light"] {
  --color-primary: #3498db;
  --color-primary-hover: #2980b9;
  --color-secondary: #7f8c8d;
  --color-secondary-hover: #6c7a7b;
  --color-danger: #e74c3c;
  --color-danger-hover: #c0392b;
  --color-edit: #f1c40f;
  --color-edit-hover: #d4ac0d;
  --color-text: #333;
  --color-headings: #2c3e50;
  --color-background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  --color-surface: #fff;
  --color-surface-alt: #ecf0f1;
  --color-border: #ddd;
  --color-container-shadow: #00000019;
  --spacing-xs: 5px;
  --spacing-sm: 10px;
  --spacing-md: 15px;
  --spacing-lg: 20px;
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
}

[data-theme="dark"] {
  --color-primary: #3498db;
  --color-primary-hover: #5dade2;
  --color-secondary: #95a5a6;
  --color-secondary-hover: #bdc3c7;
  --color-danger: #e74c3c;
  --color-danger-hover: #f1948a;
  --color-edit: #f1c40f;
  --color-edit-hover: #f5d76e;
  --color-text: #ecf0f1;
  --color-headings: #fff;
  --color-background: linear-gradient(135deg, #2c3e50 0%, #4b6584 100%);
  --color-surface: #34495e;
  --color-surface-alt: #2c3e50;
  --color-border: #4b6584;
  --color-container-shadow: #0006;
}

body {
  background-image: var(--color-background);
  color: var(--color-text);
  padding: var(--spacing-lg);
  background-size: cover;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  line-height: 1.6;
  transition: background .2s ease-in-out, color .2s ease-in-out;
}

.container {
  background: var(--color-surface);
  max-width: 700px;
  padding: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  box-shadow: 0 2px 10px var(--color-container-shadow);
  margin: auto;
}

h1, h2 {
  color: var(--color-headings);
}

.form-group {
  margin-bottom: var(--spacing-md);
}

label {
  margin-bottom: var(--spacing-xs);
  display: block;
}

input[type="text"], #note-title, textarea {
  width: 100%;
  padding: var(--spacing-sm);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-sm);
  box-sizing: border-box;
  background-color: var(--color-surface);
  color: var(--color-text);
}

input[type="text"]::placeholder, #note-title::placeholder, textarea::placeholder {
  color: var(--color-text);
}

.btn {
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  color: var(--color-surface);
  margin-right: var(--spacing-xs);
  border: none;
  text-decoration: none;
  transition: background-color .2s ease-in-out;
  display: inline-block;
}

.btn-primary {
  background-color: var(--color-primary);
}

.btn-primary:hover {
  background-color: var(--color-primary-hover);
}

.btn-secondary {
  background-color: var(--color-secondary);
}

.btn-secondary:hover {
  background-color: var(--color-secondary-hover);
}

.btn-danger {
  background-color: var(--color-danger);
}

.btn-danger:hover {
  background-color: var(--color-danger-hover);
}

.btn-edit {
  background-color: var(--color-edit);
}

.btn-edit:hover {
  background-color: var(--color-edit-hover);
}

#notes-list {
  padding: 0;
  list-style: none;
}

#notes-list li {
  background: var(--color-surface-alt);
  padding: var(--spacing-md);
  margin-bottom: var(--spacing-sm);
  border-radius: var(--border-radius-sm);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#notes-list li .note-content {
  flex-grow: 1;
}

#notes-list li h3 {
  margin: 0 0 var(--spacing-xs) 0;
}

.app-header {
  max-width: 700px;
  margin: 0 auto var(--spacing-lg);
  padding-bottom: var(--spacing-md);
  border-bottom: 1px solid var(--color-border);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.app-header .logo {
  color: var(--color-headings);
  margin: 0;
  font-size: 1.5rem;
  text-decoration: none;
}

.main-nav a {
  margin-left: var(--spacing-md);
  color: var(--color-text);
  text-decoration: none;
  transition: color .2s ease-in-out;
}

.main-nav a:hover {
  color: var(--color-primary);
}

#theme-toggle-btn {
  background-color: var(--color-secondary);
  color: var(--color-surface);
  margin-left: var(--spacing-md);
}

#theme-toggle-btn:hover {
  background-color: var(--color-secondary-hover);
}

.app-footer {
  max-width: 700px;
  margin: var(--spacing-lg) auto 0;
  padding-top: var(--spacing-md);
  border-top: 1px solid var(--color-border);
  text-align: center;
  color: var(--color-text);
  justify-content: space-between;
  align-items: center;
  font-size: .9rem;
  display: flex;
}

.footer-nav a {
  margin-left: var(--spacing-md);
  color: var(--color-text);
  text-decoration: none;
  transition: color .2s ease-in-out;
}

.footer-nav a:hover {
  color: var(--color-primary);
}

.error {
  color: var(--color-danger);
  font-weight: bold;
}

@media (width <= 600px) {
  #notes-list li {
    flex-direction: column;
    align-items: flex-start;
  }

  #notes-list li .note-actions {
    margin-top: var(--spacing-md);
    justify-content: flex-end;
    width: 100%;
    display: flex;
  }
}
